.course-detail {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  color: var(--text-primary);
}

.course-header {
  margin-bottom: 3rem;
}

.course-header h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
  color: var(--text-primary);
  font-weight: 600;
}

.course-description {
  font-size: 1.1rem;
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.course-meta {
  display: flex;
  gap: 2rem;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.course-meta span {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 8px;
}

.course-content {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  padding: 2rem;
  margin-bottom: 3rem;
}

.course-content h2 {
  font-size: 1.75rem;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
}

.modules {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.module {
  background: rgba(255, 255, 255, 0.03);
  border-radius: 12px;
  padding: 1.5rem;
}

.module h3 {
  font-size: 1.25rem;
  margin-bottom: 1rem;
  color: var(--text-primary);
}

.module ul {
  list-style: none;
  padding: 0;
}

.module li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.75rem;
  border-radius: 8px;
  margin-bottom: 0.5rem;
  background: rgba(255, 255, 255, 0.02);
  transition: all 0.3s ease;
}

.module li:hover {
  background: rgba(255, 255, 255, 0.05);
}

.lesson-title {
  color: var(--text-secondary);
  font-size: 0.95rem;
}

.lesson-duration {
  color: var(--text-secondary);
  font-size: 0.85rem;
  padding: 0.25rem 0.75rem;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 15px;
}

.course-features,
.course-requirements {
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  padding: 2rem;
  margin-bottom: 2rem;
}

.course-features h2,
.course-requirements h2 {
  font-size: 1.75rem;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
}

.course-features ul,
.course-requirements ul {
  list-style: none;
  padding: 0;
  display: grid;
  gap: 1rem;
}

.course-features li,
.course-requirements li {
  color: var(--text-secondary);
  padding: 1rem;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 8px;
  font-size: 0.95rem;
  line-height: 1.5;
}

.enroll-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 16px;
  padding: 2rem;
  margin-top: 3rem;
}

.enroll-button {
  padding: 1rem 2.5rem;
  border: none;
  border-radius: 8px;
  background: linear-gradient(135deg, var(--primary), var(--primary-dark));
  color: white;
  font-size: 1.1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
}

.enroll-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 15px rgba(0, 180, 219, 0.3);
}

.price {
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-primary);
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50vh;
  font-size: 1.2rem;
  color: var(--text-secondary);
}

.error {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50vh;
  font-size: 1.2rem;
  color: var(--text-secondary);
  text-align: center;
  padding: 2rem;
  background: rgba(255, 0, 0, 0.1);
  border-radius: 16px;
}

@media (max-width: 768px) {
  .course-detail {
    padding: 1rem;
  }

  .course-header h1 {
    font-size: 2rem;
  }

  .course-meta {
    flex-direction: column;
    gap: 1rem;
  }

  .course-content,
  .course-features,
  .course-requirements {
    padding: 1.5rem;
  }

  .enroll-section {
    flex-direction: column;
    gap: 1.5rem;
    text-align: center;
  }

  .enroll-button {
    width: 100%;
  }
}
