.courses {
  padding: 2rem;
  padding-top: 90px;
  max-width: 1400px;
  margin: 0 auto;
}

.courses-header {
  margin-bottom: 2rem;
}

.courses-header h1 {
  font-size: 2.5rem;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
}

.search-container {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
}

.search-input {
  flex: 1;
  padding: 0.75rem 1rem;
  border-radius: 8px;
  background: rgba(15, 23, 42, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.08);
  color: var(--text-primary);
  font-size: 1rem;
}

.search-input::placeholder {
  color: var(--text-secondary);
}

.category-select {
  padding: 0.75rem 1rem;
  border-radius: 8px;
  background: rgba(15, 23, 42, 0.6);
  border: 1px solid rgba(255, 255, 255, 0.08);
  color: var(--text-primary);
  font-size: 1rem;
  cursor: pointer;
  min-width: 150px;
}

.courses-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  gap: 2rem;
}

.course-card {
  background: rgba(15, 23, 42, 0.6);
  border-radius: 16px;
  overflow: hidden;
  border: 1px solid rgba(255, 255, 255, 0.08);
  transition: transform 0.2s ease;
}

.course-card:hover {
  transform: translateY(-4px);
}

.course-image {
  width: 100%;
  height: 160px;
  background: var(--bg-darker);
  border-radius: 12px 12px 0 0;
  overflow: hidden;
}

.course-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.course-card:hover .course-image img {
  transform: scale(1.05);
}

.course-content {
  padding: 1.5rem;
}

.course-icon {
  width: 100%;
  height: 120px;
  margin-bottom: 1.5rem;
  background: rgba(76, 111, 255, 0.1);
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1.5rem;
  transition: transform 0.2s ease;
}

.course-card:hover .course-icon {
  transform: translateY(-4px);
}

.course-icon img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.course-title {
  font-size: 1.5rem;
  color: var(--text-primary);
  margin-bottom: 0.75rem;
  line-height: 1.3;
}

.course-description {
  color: var(--text-secondary);
  font-size: 0.95rem;
  margin-bottom: 1.5rem;
  line-height: 1.6;
}

.course-meta {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  margin: 1.5rem 0;
  padding: 1rem 0;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
}

.meta-item {
  text-align: center;
}

.meta-value {
  font-size: 1.25rem;
  color: var(--text-primary);
  font-weight: 600;
  margin-bottom: 0.25rem;
}

.meta-label {
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.course-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.course-price {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--text-primary);
}

.course-price.free {
  color: #10B981;
}

.course-level {
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.course-level svg {
  width: 16px;
  height: 16px;
}

.course-level.Beginner {
  background: rgba(16, 185, 129, 0.1);
  color: #10B981;
}

.course-level.Intermediate {
  background: rgba(245, 158, 11, 0.1);
  color: #F59E0B;
}

.course-level.Advanced {
  background: rgba(239, 68, 68, 0.1);
  color: #EF4444;
}

.course-level.All {
  background: rgba(99, 102, 241, 0.1);
  color: #6366F1;
}

.enroll-button {
  width: 100%;
  padding: 0.875rem;
  margin-top: 1rem;
  border-radius: 8px;
  background: var(--primary);
  color: white;
  font-size: 1rem;
  font-weight: 500;
  border: none;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.enroll-button:hover {
  background: var(--primary-dark);
}

.duration {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-secondary);
  font-size: 0.95rem;
}

.duration-icon {
  width: 20px;
  height: 20px;
}

@media (max-width: 768px) {
  .courses {
    padding: 1rem;
  }

  .search-container {
    flex-direction: column;
  }

  .category-select {
    width: 100%;
  }

  .courses-grid {
    grid-template-columns: 1fr;
  }

  .course-card {
    max-width: 100%;
  }
}
